@import url(https://fonts.googleapis.com/css2?family=Play:wght@400;700&display=swap);
* {
    margin: 0;
    padding: 0;
}

html, body {
    height: 100vh;
}

body {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: lightblue;
}

.header {
    font-size: 2.0rem;
    text-align: center;
    color: darkgoldenrod;
    margin: 20px;
}

.footer {
    font-size: 0.9rem;
    text-align: center;
    margin: 5px;
}

.footer a {
    text-decoration: none;
    color: inherit;
}

.footer a:hover {
    cursor: pointer;
    color: orangered;
}
.App {
    width: 330px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    background-color: #0f0f0f;
    padding: 5px;
    border-radius: 5%;
    font-size: 24px;
}

.display {
    width: 100%;
    margin-bottom: 5px;
    margin-right: 5px;
    display: inline-block;
    border: none;
    text-align: right;
    background-color: inherit;
    overflow: hidden;
    font-family: 'Play', sans-serif;
}

.grid-container {
    display: grid;
    grid-gap: 1px;
    grid-template-columns: 80px 80px 80px 80px;
    grid-template-rows: 50px 50px 50px 50px 50px;
    justify-content: center;
    width: 100%;
}

#preview {
    color: lightyellow;
    font-weight: 1.2rem;
}

#display {
    color: yellow;
    font-size: 1.8rem;
}

.button {
    font-family: 'Play', sans-serif;
    display: inline-block;
    margin: 1px;
    background-color: red;
    color: white;
    font-size: 1.2em;

}

.button:hover {
    cursor: pointer;
}

.button:hover {
    opacity: 0.75;
}

#clear {
    grid-column: 4 / 4;
    grid-row: 2 / 5;
}

#equals {
    grid-column: 3 / 5;
    grid-row: 5 / 5;
}

#zero {
    grid-row: 5 / 5;
    grid-column: 2 / 3;
}
